.logo-container{
  position: relative;
    margin-top: 18%;
    left: 55%;
    border-radius: 50%;
    width: 400px;
    z-index: 1;
   
  }
   .logo-container img {
    position: absolute;
    top: auto;
    right: auto;
    bottom: auto;
    left: 0;
    margin: auto;
    z-index: 1;
    width: 400px;
    border-radius: 50%;
     border: 6px solid #3b3b3b;

}
@media screen and (max-width: 1200px) {

  .logo-container{
    position: absolute;
    margin-top: -37%;
  .my-pic{
    right: auto;
    bottom: 10%;
    left: -42%;
    z-index: -1;
    width: 70%;
    }
  }
  
}