

.home-page {
  width: 78%;
  display: flex;
  justify-content: center;
  align-items: center;
    .text-zone {
      position: absolute;
      left: 30%;
      top: 53%;
      transform: translateY(-50%);
      width: 40%;
      max-height: 90%;
    }
  
    h1 {
      color: #fff;
      font-size: 56px;
      line-height: 53px;
      margin: 0;
      font-family: 'Coolvetica';
      font-weight: 400;
  
      img {
        margin-left: 10px;
        width: 32px;
        height: auto;
        animation: rotateIn 1s linear both;
        animation-delay: 1.4s;
      
      }
    }
  
  
    .flat-button {
      color: greenyellow;
      font-size: 13px;
      font-weight: 400;
      letter-spacing: 4px;
      font-family: sans-serif;
      text-decoration: none;
      padding: 10px 18px;
      border: 1px solid greenyellow;
      margin-top: 25px;
      float: left;
      white-space: nowrap;
      
  
      
      &:hover {
        background: greenyellow;
        transform: translateX(-100px);
        color: #333;
      }
    }
    .wrapper {
      // height: 100vh;
      /*This part is important for centering*/
      display: grid;
      place-items: center;
    }
    
    .typing-demo {
      width: 19ch;
      animation: appear 15s linear 2s forwards, typing 2s steps(18), blink .5s step-end infinite alternate;
      white-space: nowrap;
      overflow: hidden;
      border-right: 3px solid;
      font-family: monospace;
      font-size: 2em;
      color: #8d8d8d;
      margin-left: -64%;
    }
    
    @keyframes typing {
      from {
        width: 0
      }
    }
        
    @keyframes blink {
      50% {
        border-color: transparent
      }
    }
  }
  
  
  
  @media screen and (max-width: 1200px) {
    .home-page{
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      .text-zone {
        transform: none;
        padding: 10px;
        box-sizing: border-box;
        position: absolute;
        margin-top: 19%;
        width: 100%;
        left: 5%;
    }
   }
    .home-page .flat-button {
      
      display: block;
      margin: 20px auto 0 auto;
      width: 124px;
    }
    .home-page .wrapper{
        position: relative;
        margin-top: 5%;
        width: 100%;
        left: 25%;
    }
}