.container{
    
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        padding-left: 0.5rem;
        padding-right: 0.5rem;   
}
.container::before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#21f367,#3d1ee9);
  clip-path: circle(50% at right 70%);
}
.container::after{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(#21f367,#3d1ee9,white);
  clip-path: circle(30% at 10% 10%);
}

