
     
  .card-container1 {
        display: flex;
        width: 100%;
        flex-shrink: 0;
        border-radius: 50px 0px 0px 0px;
        margin-top: 5%;
        position: absolute;
        top: 1%;
        left: 47%;

        .card1,
        .card2,
        .card3{
        background-color: #ffe6e6;
        width: 15%;
        height: 299px;
        flex-shrink: 0;
        border-radius: 30px;
        background: #FFF;
        box-shadow: 0px 4px 15px 0px rgba(241, 25, 25, 0.71), 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
        display: flex;
        flex-direction: column;
        align-items: center;  // Center content horizontally
        justify-content: center; 
        }
        .card1 {
        
        margin-left: 2%;
    
        }
  
        .card2 {
        
       
        margin-left: 2%;
         }
  
        .card3 {
        
        margin-left: 2%;
        }
}
        .card1:hover,
        .card2:hover,
        .card3:hover,
        .card4:hover,
        .card5:hover,
        .card6:hover,
        .card7:hover,
        .card8:hover,
        .card9:hover{
            cursor: pointer;
            transform: rotateY(180deg);
        }
        .card1,
        .card2,
        .card3,
        .card4,
        .card5,
        .card6,
        .card7,
        .card8,
        .card9{
            position: relative;
            transition: transform 1500ms;
            transform-style: preserve-3d;
        }
        .front,
        .card-body{
            position: absolute;
            backface-visibility: hidden;
        }
        .card-body{
            border-radius: 2rem;
            background-color: #3a3a3a;
            transform: rotateY(180deg);
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            font-size: 16px;
            color: #ebf1f1;
            height: 299px;
            .card-title{
                font-size: 24px;
                color: #ebf1f1;
            }
            .card-text{
                font-size: 24px;
                font-style: italic;
                font-family: Georgia, 'Times New Roman', Times, serif;
                margin-left: 10%;
            }
            .button{
                list-style: none;
                font-size: 16px;
                color:#ebf1f1 ;
            }
        }
       

